import React, { useState, useEffect, useRef } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { API_ENDPOINTS } from '../../config/api';
import { checkAllTermsAgreement, saveTermsAgreement } from '../../utils/termsAgreement';
import TermsAgreementModal from '../../components/terms/TermsAgreementModal';
import LandingPage from './LandingPage';
import IntroLandingPage from './IntroLandingPage';
import ReadyModal from './ReadyModal';

type PageType = 'landing' | 'result' | 'intro' | 'payment' | 'terms';

//    
const ReportGeneratingPage: React.FC<{ status: PartnerStatus | null; onMount?: () => void }> = ({ status, onMount }) => {
  const location = useLocation();
  
  useEffect(() => {
    //     (ReadyModal  )
    if (onMount) {
      onMount();
    }
  }, [onMount]);
  
  useEffect(() => {
    if (status?.has_report && status?.redirect_url) {
      console.log('[DiseasePrediction]   , :', status.redirect_url);
      window.location.href = status.redirect_url;
      return;
    }
    
    // 3  
    const checkInterval = setInterval(async () => {
      try {
        const urlParams = new URLSearchParams(location.search);
        const uuid = urlParams.get('uuid');
        const partner = urlParams.get('partner');
        const apiKey = urlParams.get('api_key');
        const oid = urlParams.get('oid');
        
        // 1. oid    API 
        if (oid) {
          console.log('[ReportGeneratingPage] oid    :', oid);
          const response = await fetch(API_ENDPOINTS.GET_REPORT(oid));
          const result = await response.json();
          
          if (result.success && (result.report_url || result.mediarc_response)) {
            clearInterval(checkInterval);
            if (result.report_url) {
              console.log('[ReportGeneratingPage]    (oid), ');
            } else if (result.mediarc_response) {
              console.log(' [ReportGeneratingPage] PDF ,  :', {
                oid: oid,
                has_mediarc_data: !!result.mediarc_response
              });
            }
            window.location.href = `/disease-report?oid=${oid}`;
            return;
          }
        }
        
        // 2. uuid/partner    API  ( )
        if (uuid && partner) {
          const checkUrl = `${API_ENDPOINTS.CHECK_PARTNER_STATUS}?uuid=${uuid}&partner=${partner}${apiKey ? `&api_key=${apiKey}` : ''}`;
          const response = await fetch(checkUrl);
          const result: PartnerStatus = await response.json();
          
          console.log('[ReportGeneratingPage]   (uuid):', result);
          
          if (result.has_report && result.redirect_url) {
            clearInterval(checkInterval);
            console.log('[ReportGeneratingPage]    (uuid), :', result.redirect_url);
            window.location.href = result.redirect_url;
          }
        }
      } catch (error) {
        console.error('[ReportGeneratingPage]   :', error);
      }
    }, 3000);
    
    return () => clearInterval(checkInterval);
  }, [status, location.search]);
  
  return (
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      minHeight: '100vh',
      padding: '20px',
      textAlign: 'center'
    }}>
      <div style={{ fontSize: '24px', marginBottom: '20px' }}></div>
      <h2 style={{ fontSize: '20px', marginBottom: '10px', color: '#333' }}>
        AI    ...
      </h2>
      <p style={{ fontSize: '14px', color: '#666', marginBottom: '30px' }}>
         .<br />
            .
      </p>
      <div className="loading-spinner" style={{
        width: '40px',
        height: '40px',
        border: '4px solid #f3f3f3',
        borderTop: '4px solid #8B7355',
        borderRadius: '50%',
        animation: 'spin 1s linear infinite'
      }} />
      <style>{`
        @keyframes spin {
          0% { transform: rotate(0deg); }
          100% { transform: rotate(360deg); }
        }
      `}</style>
    </div>
  );
};

export interface PartnerStatus {
  case_id: string;
  action: string;
  redirect_url: string;
  has_report: boolean;
  has_checkup_data: boolean;
  has_payment: boolean;
  requires_payment: boolean;
  payment_amount?: number; //    (DB )
  partner_id?: string;     //   ID
  is_welno_user: boolean;
  error_message?: string;  //    
  failed_oid?: string;     //   OID
}

const DiseasePredictionCampaign: React.FC = () => {
  const [currentPage, setCurrentPage] = useState<PageType>('intro'); //  'intro'  IntroLandingPage  
  const [loading, setLoading] = useState(true);
  const [status, setStatus] = useState<PartnerStatus | null>(null);
  const [showTermsModal, setShowTermsModal] = useState(false);
  const [showReadyModal, setShowReadyModal] = useState(false);  //    
  const navigate = useNavigate();
  const location = useLocation();

  //  / 
  useEffect(() => {
    const handlePopState = (event: PopStateEvent) => {
      // popstate   URL   
      // location.search    
      const urlParams = new URLSearchParams(location.search);
      const page = urlParams.get('page');
      
      console.log('[DiseasePrediction]  / :', { page, location: location.search });
      
      if (page && ['payment', 'landing', 'terms', 'intro', 'result'].includes(page)) {
        setCurrentPage(page as PageType);
        setLoading(false);
        console.log('[DiseasePrediction]  :', page);
      } else if (!page) {
        setCurrentPage('intro');
        setLoading(false);
        console.log('[DiseasePrediction] intro  ');
      }
    };

    window.addEventListener('popstate', handlePopState);
    return () => {
      window.removeEventListener('popstate', handlePopState);
    };
  }, [location.search]);

  useEffect(() => {
    const checkUserStatus = async () => {
      const urlParams = new URLSearchParams(location.search);
      const page = urlParams.get('page'); // string | null
      const partner = urlParams.get('partner');
      const uuid = urlParams.get('uuid');
      const oid = urlParams.get('oid');
      const data = urlParams.get('data');
      const apiKey = urlParams.get('api_key');
      
      console.log('[DiseasePrediction] URL  :', { 
        page, partner, uuid, oid,
        data_exists: !!data, 
        data_length: data?.length || 0,
        apiKey: !!apiKey,
        currentPage_before: currentPage
      });
      
      //  page=result oid  uuid     
      if (page === 'result' && oid) {
        console.log('[DiseasePrediction] page=result & oid ,   ');
        setCurrentPage('result');
        setLoading(false);
        return;
      }
      
      // page    
      if (!page) {
        //  payment terms    (      )
        if (currentPage === 'payment' || currentPage === 'terms') {
          console.log('[DiseasePrediction] page     :', currentPage);
          setLoading(false);
          return;
        }
        //  page  intro  ,    
        console.log('[DiseasePrediction] page  , intro    ');
        setCurrentPage('intro');
        // return  -      
      }

      // 1.        
      // (         )

      // result   -    intro   

      // 2. UUID    API  (partner    )
      if (uuid) {
        try {
          const response = await fetch(API_ENDPOINTS.CHECK_PARTNER_STATUS, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
              partner_id: partner || null,
              api_key: apiKey || null,
              uuid: uuid,
              encrypted_data: data || null
            })
          });

          if (!response.ok) throw new Error('  ');

          const result: PartnerStatus = await response.json();
          console.log('[DiseasePrediction]   :', result);
          setStatus(result);

          // action  
          if (result.action === 'show_report') {
            console.log('[DiseasePrediction]  ,   :', result.redirect_url);
            window.location.href = result.redirect_url;
            return;
          }

          //     
          if (result.action === 'show_payment_failed') {
            console.log('[DiseasePrediction]   :', result.error_message);
            alert(` \n\n: ${result.error_message}\n\n .`);
            setCurrentPage('intro');
            setLoading(false);
            return;
          }

          //      
          if (result.action === 'show_ready_modal') {
            console.log('[DiseasePrediction]   ,  ');
            setShowReadyModal(true);
            setCurrentPage('intro');
            setLoading(false);
            return;
          }

          // redirect_to_auth       
          // (   )
          if (result.action === 'redirect_to_auth') {
            console.log('[DiseasePrediction]    (    )');
            //    
            //         ,         
          }

          // show_terms_modal       
          // (   )
          if (result.action === 'show_terms_modal') {
            console.log('[DiseasePrediction]    (    )');
            //    
            //       
          }
          
          // URL  page   ,  intro
          const validPages: PageType[] = ['payment', 'landing', 'terms', 'intro', 'result'];
          if (page && validPages.includes(page as PageType)) {
            console.log('[DiseasePrediction] currentPage  (  ):', page, ' currentPage:', currentPage);
            // page     (navigate   )
            setCurrentPage(page as PageType);
          } else {
            console.log('[DiseasePrediction] currentPage : intro (page  )');
            // page    intro  ( payment terms   )
            if (currentPage !== 'payment' && currentPage !== 'landing' && currentPage !== 'terms') {
              setCurrentPage('intro');
            }
          }
          
        } catch (error) {
          console.error('[DiseasePrediction]   :', error);
          setCurrentPage('intro');
        }
      } else {
        //     intro ( )
        setCurrentPage('intro');
      }

      setLoading(false);
      console.log('[DiseasePrediction] loading : false ');
    };

    checkUserStatus();
  }, [navigate, location.search]);

  //  URL  localStorage  
  useEffect(() => {
    const urlParams = new URLSearchParams(location.search);
    const clearStorage = urlParams.get('clear_storage');
    
    if (clearStorage === 'true') {
      console.log('[] URL  localStorage  ');
      
      const keysToRemove = [
        'collectingStatus',
        'welno_password_modal_open',
        'welno_tilko_auth_waiting',
        'welno_tilko_auth_method_selection',
        'welno_tilko_info_confirming',
        'manualCollect'
      ];
      
      let removedCount = 0;
      keysToRemove.forEach(key => {
        if (localStorage.getItem(key)) {
          localStorage.removeItem(key);
          removedCount++;
        }
      });
      
      console.log('[] URL  localStorage  :', { removedCount });
      
      // URL clear_storage  
      urlParams.delete('clear_storage');
      const newUrl = `${window.location.pathname}${urlParams.toString() ? '?' + urlParams.toString() : ''}`;
      window.history.replaceState({}, '', newUrl);
      
      //    
      setTimeout(() => {
        window.location.reload();
      }, 500);
    }
  }, [location.search]);

  useEffect(() => {
    document.title = 'Xog:  - AI  ';
  }, []);

  //   currentPage   
  useEffect(() => {
    const handlePageChange = (event: CustomEvent<{ page: PageType }>) => {
      const { page } = event.detail;
      console.log('[DiseasePrediction]   currentPage  :', page);
      if (page === 'payment' || page === 'landing' || page === 'terms') {
        setCurrentPage(page);
        setLoading(false);
      }
    };

    window.addEventListener('welno-campaign-page-change', handlePageChange as EventListener);
    return () => {
      window.removeEventListener('welno-campaign-page-change', handlePageChange as EventListener);
    };
  }, []);

  // page=terms    
  useEffect(() => {
    if (currentPage === 'terms') {
      setShowTermsModal(true);
    }
  }, [currentPage]);

  // currentPage        DOM 
  useEffect(() => {
    if (currentPage === 'payment') {
      console.log('[DiseasePrediction] payment  ,    ');
      console.log('[DiseasePrediction] DOM  :', {
        container: document.querySelector('[data-current-page="payment"]'),
        paymentPage: document.querySelector('[data-page="payment"]'),
        introPage: document.querySelector('[data-page="intro"]'),
        companyInfo: document.querySelector('.company-info')
      });
      
      //   (smooth  auto )
      window.scrollTo({ top: 0, behavior: 'auto' });
      
      //       
      setTimeout(() => {
        const container = document.querySelector('[data-current-page="payment"]');
        const paymentPage = document.querySelector('[data-page="payment"]');
        const introPage = document.querySelector('[data-page="intro"]');
        const companyInfo = document.querySelector('.company-info');
        
        console.log('[DiseasePrediction] 100ms  DOM :', { 
          container: !!container, 
          paymentPage: !!paymentPage,
          introPage: !!introPage, // intro  
          companyInfo: !!companyInfo // company-info  LandingPage   
        });
        
        if (introPage) {
          console.error('[DiseasePrediction]  intro   DOM !');
        }
        
        if (!companyInfo) {
          console.error('[DiseasePrediction]  company-info  ! LandingPage     ');
        }
        
        if (paymentPage) {
          paymentPage.scrollIntoView({ behavior: 'smooth', block: 'start' });
        } else if (container) {
          container.scrollIntoView({ behavior: 'smooth', block: 'start' });
        }
      }, 100);
    }
  }, [currentPage]);

  //          
  // renderContent    JSX   currentPage  
  console.log('[DiseasePrediction] :', { currentPage, loading, hasStatus: !!status });
  
  let content;
  if (currentPage === 'result') {
    console.log(' [DiseasePrediction] =====     =====');
    content = <ReportGeneratingPage status={status} onMount={() => setShowReadyModal(false)} />;
  } else if (currentPage === 'payment') {
    console.log(' [DiseasePrediction] ===== LandingPage  (payment) =====');
    console.log(' [DiseasePrediction]     " 10  ...",  ""   ');
    content = <LandingPage key="payment-page" status={status} />;
  } else if (currentPage === 'terms') {
    console.log(' [DiseasePrediction] ===== IntroLandingPage  (terms) =====');
    content = <IntroLandingPage key="terms-page" status={status} />;
  } else if (currentPage === 'intro') {
    console.log(' [DiseasePrediction] ===== IntroLandingPage  (intro) =====');
    console.log(' [DiseasePrediction]     " 20 ...", ""   ');
    content = <IntroLandingPage key="intro-page" status={status} />;
  } else {
    console.log(' [DiseasePrediction] ===== LandingPage  (, currentPage:', currentPage, ') =====');
    content = <LandingPage key="landing-default" />;
  }

  const urlParams = new URLSearchParams(location.search);
  const uuid = urlParams.get('uuid');
  const urlPartner = urlParams.get('partner');
  const apiKey = urlParams.get('api_key');
  const data = urlParams.get('data');
  // status partner_id   (   )
  const partner = status?.partner_id || urlPartner || 'kindhabit';

  return (
    <div 
      style={{ position: 'relative', minHeight: '100vh' }} 
      data-current-page={currentPage}
      key={`container-${currentPage}`} // currentPage     
    >
      {content}
      
      {/*   (page=terms  ) */}
      {showTermsModal && (
        <TermsAgreementModal
          isOpen={showTermsModal}
          onClose={() => {
            setShowTermsModal(false);
            //   
            const params = new URLSearchParams();
            if (uuid) params.set('uuid', uuid);
            if (partner) params.set('partner', partner);
            if (apiKey) params.set('api_key', apiKey);
            if (data) params.set('data', data);
            navigate(`/campaigns/disease-prediction?${params.toString()}`);
          }}
          onConfirm={async (agreedTerms: any, termsAgreement: any) => {
            //  
            if (uuid && termsAgreement && partner) {
              await saveTermsAgreement(
                uuid,
                partner,
                {
                  terms_service: termsAgreement.terms_service,
                  terms_privacy: termsAgreement.terms_privacy,
                  terms_sensitive: termsAgreement.terms_sensitive,
                  terms_marketing: termsAgreement.terms_marketing,
                },
                undefined, // oid 
                undefined, // userInfo 
                apiKey || undefined
              );
            }
            
            //   
            setShowTermsModal(false);
            
            // navigate  location.search   currentPage      
            window.dispatchEvent(new CustomEvent('welno-campaign-page-change', { 
              detail: { page: 'payment' } 
            }));
            
            //    (  , replace: false  )
            const params = new URLSearchParams();
            params.set('page', 'payment');
            if (uuid) params.set('uuid', uuid);
            if (partner) params.set('partner', partner);
            if (apiKey) params.set('api_key', apiKey);
            if (data) params.set('data', data);
            navigate(`/campaigns/disease-prediction?${params.toString()}`, { replace: false });
          }}
        />
      )}
      
      {/*      */}
      {showReadyModal && status && (
        <ReadyModal
          onGenerate={async () => {
            try {
              const query = new URLSearchParams(location.search);
              const uuid = query.get('uuid');
              const partner = query.get('partner');
              const oid = query.get('oid');
              const apiKey = query.get('api_key');

              // oid  uuid + partner  
              console.log('[ReadyModal]   :', { oid, uuid, partner, hasApiKey: !!apiKey });

              //   API  (oid  uuid + partner )
              const response = await fetch(API_ENDPOINTS.GENERATE_REPORT, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ 
                  oid: oid || undefined,
                  uuid,
                  partner_id: partner,
                  api_key: apiKey || undefined
                })
              });

              const result = await response.json();

              if (result.success) {
                console.log('[ReadyModal]    ');
                setShowReadyModal(false);
                
                //     (   DB )
                const resultUrl = oid 
                  ? `/disease-report?oid=${oid}`
                  : `/disease-report?uuid=${uuid}&hospital_id=PEERNINE`;
                
                console.log('[ReadyModal]   :', resultUrl);
                window.location.href = resultUrl;
              } else {
                alert('   : ' + result.detail);
              }
            } catch (error) {
              console.error('[ReadyModal]   :', error);
              alert('   .');
            }
          }}
          onClose={() => setShowReadyModal(false)}
          userName={status?.partner_id}
        />
      )}

      {/*    (    ) */}
      {loading && !status && (
        <div style={{ 
          position: 'fixed',
          top: 0, left: 0, right: 0, bottom: 0,
          display: 'flex', 
          justifyContent: 'center', 
          alignItems: 'center', 
          backgroundColor: 'rgba(255, 255, 255, 0.7)',
          zIndex: 9999,
          fontSize: '18px',
          color: '#666'
        }}>
           ...
        </div>
      )}
      {/* :    */}
      {loading && status && (
        <div style={{
          position: 'fixed',
          top: 10,
          right: 10,
          padding: '10px',
          backgroundColor: 'orange',
          color: 'white',
          zIndex: 10000,
          fontSize: '12px'
        }}>
           loading=true, status  (  )
        </div>
      )}
      
    </div>
  );
};

export default DiseasePredictionCampaign;
